import React from "react";
import { useEffect, useState } from "react";
import "./Slide.css";
import axios from "axios";

export const Slide = React.memo(function (StackedCarouselSlideProps) {
    const {
        data,
        dataIndex,
        isCenterSlide,
        swipeTo,
        slideIndex
    } = StackedCarouselSlideProps;

    const coverImage = 'https://early-icons-roll-35-230-119-109.loca.lt/view_image?id=' + data[dataIndex].image_id;
    const document_type = data[dataIndex].document_type;
    const confidence_level = !isNaN(data[dataIndex].confidence_level) ?
        (Math.round(data[dataIndex].confidence_level * 100) / 100) + "%" :
        data[dataIndex].confidence_level;

    const [imgSrc, setImgSrc] = useState([]);

    useEffect(() => {
        axios.get(coverImage, {
            responseType: "blob",
            headers: {
                'Bypass-Tunnel-Reminder': 'abc'
            }
        })
            .then(function (response) {

                var reader = new window.FileReader();
                reader.readAsDataURL(response.data);
                reader.onload = function () {
                    var imageDataUrl = reader.result;
                    setImgSrc(imageDataUrl)
                }
            });
    }, []);

    return (
        <div className="card-card" draggable={false} style={{ width: "100%", height: "100%" }}>
            <div className={`cover fill ${isCenterSlide ? "off" : "on"}`}>
                <div
                    className="card-overlay fill"
                    onClick={() => {
                        if (!isCenterSlide) swipeTo(slideIndex);
                    }}
                />
            </div>
            <div className="detail fill">
                <div className="discription">
                    <img
                        style={{ width: "100%", height: "100%" }}
                        alt="Loading, please wait"
                        className="cover-image"
                        src={imgSrc}
                    />
                    <div style={{ backgroundColor: "#1B1E23" }}>
                        <p style={{ color: "white", fontSize: "12px", fontWeight: "bolder" }}>{document_type}</p>
                        <p style={{ color: "white", fontSize: "12px", fontWeight: "bold" }}>{confidence_level}</p>
                    </div>
                </div>
            </div>
        </div>
    );
});
